.page {

  scroll-behavior: smooth;

  &__section {
    padding-bottom: 65px;
  }

  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    transform: translateY(-100%);
    transition: transform 0.3s;

    &:target {
      transform: translateY(0%);
    }
  }

  &__body--with-menu {
    overflow: hidden;
  }
}
